@import url(https://fonts.googleapis.com/css?family=Alfa+Slab+One|Roboto);
:root{
  /* ==== Color ==== */
  --darkGreen: rgba(4, 42, 43, 1);
  --bone: rgba(228, 221, 212, 1);
  --outerSpace: rgba(57, 76, 76, 1);
  --alabaster: rgba(240, 239, 226, 1);
  --prusianBlue: rgba(11, 57, 84, 1);
      /*Text*/
  --whiteText: rgba(255, 255, 255, .85);
  --whiteText-muted: rgba(255, 255, 255, .6);
  --blackText: rgba(0, 0, 0, .85);
  --blackText-muted: rgba(0, 0, 0, .5);

  /* ==== Space ==== */
  --space-xs: .1rem;
  --space-sm: .5rem; 
  --space: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 4rem;
  --space-xxl: 8rem; 

    /* Border radius */
  --borderRadius-sm: 2px;
  --borderRadius-md: 8px;

  --border-sm-solid: 2px solid;
  --border-md-solid: 4px solid;

  /* ==== Typography ==== */
  --fontTitle: 'Alfa Slab One', cursive;
  --fontText: 'Roboto', sans-serif;

  --fontSize-help: 11px;
  --fontSize-paragraph: 14px;
  --fontSize-menu: 18px;
  --fontSize-subtitle: 24px;
  --fontSize-title: 36px;
}

