.app {
  /* height: 100vh;*/
  /* overflow: auto;  */
}

.hero {
  padding: 0 3% 0 10%;
  max-width: 100%;
  /* height: auto; */
}
.nav {
  padding: var(--space-xl);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-content: center;
          align-content: center;
}

.nav__links {
  margin: auto 10% 0 0;
}

.nav__link {
  color: var(--alabaster);
  font-family: var(--fontText);
  font-size: var(--fontSize-menu);
  text-transform: uppercase;
  text-decoration: none; 
}

.nav__link:first-child {
  margin-right: var(--space-xl)
}

.nav__link:hover {
  border-bottom: 2px solid var(--alabaster);  
}
.launches {
  padding: var(--space-md);
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0;
  grid-gap: var(--space-md);
  background-color: var(--bone);
}

.launchItem {
  grid-column: 1/1;
}

.launchItem2 {
  grid-column: 2/2;
}

.launchItem,
.launchItem2 {
  list-style: none;
  border-radius: var(--borderRadius-sm);
  background-color: var(--alabaster);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.12);
  transition: all 300ms ease-out;
  padding: var(--space);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; 
  -webkit-align-items: center; 
          align-items: center;
  width: -webkit-fill-available;
}

.launchItem:hover,
.launchItem2:hover {
  box-shadow: 0 5px 14px rgba(0, 0, 0, 0.2);
  opacity: 1;
  background-image: linear-gradient( -175deg,  rgba(255,255,255,0) 50%, rgba(0,0,0,.1) 100% );
}

.launchItem_img {
  -webkit-filter: drop-shadow(0px 0px 10px rgba(0,0,0,.5));
          filter: drop-shadow(0px 0px 10px rgba(0,0,0,.5));
}

.launchItem-body {
  padding: var(--space-md) 0 var(--space-md) var(--space-md);
  width: -webkit-fill-available;
}

.launchItem_date {
  font-family: var(--fontText);
  color: var(--blackText-muted);
  text-transform: uppercase;
  font-size: var(--fontSize-menu);
}

.launchItem_icon {
  margin-left: auto;
  margin-bottom: auto;
  margin-top: -2px;
  opacity: 0;
  transition: all 300ms ease-out;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.launchItem:hover .launchItem_icon,
.launchItem2:hover .launchItem_icon {
  margin-top: -2px;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.launch_icon-success {
  -webkit-filter: saturate(0);
          filter: saturate(0);
}

.launch_icon-fail{
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.launchItem_title {
  font-family: var(--fontTitle);
  font-size: var(--fontSize-subtitle);
  color: var(--blackText);
}

.launchItem_desc {
  font-family: var(--fontText);
  color: var(--blackText);
  font-size: var(--fontSize-paragraph);
}

.launchItem-bottom_pers {
  position: absolute;
  opacity: 1;
  transition: all 300ms ease-out;
}

.launchItem:hover .launchItem-bottom_pers,
.launchItem2:hover .launchItem-bottom_pers {
  opacity: 0;
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

.launchItem-bottom_reveal {
  opacity: 0;
  -webkit-transform: translateY(8px);
          transform: translateY(8px);
  transition: all 300ms ease-out;
}

.launchItem:hover .launchItem-bottom_reveal,
.launchItem2:hover .launchItem-bottom_reveal {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.launchItem_site,
.launchItem_rocket {
  display: block;
  font-family: var(--fontText);
  color: var(--blackText-muted);
}

.rockets-table {
  border-radius: var(--borderRadius-md);
  border-collapse: collapse;
  margin-top: var(--space-xl);
  /* height: 320px; */
  /* margin: auto; */
  /* max-width: 600px; */
  padding: var(--space-sm);
  width: 100%;
  /* animation: float 5s infinite;*/
} 
 
th {
  color: var(--whiteText);
  background: var(--prusianBlue);
  border-bottom: var(--border-md-solid) var(--darkGreen);
  border-right: var(--border-sm-solid) var(--darkGreen);
  font-family: var(--fontText);
  font-size: var(--fontSize-subtitle);
  text-transform: uppercase;
  padding: var(--space-md);
  text-align: left;
  text-shadow: 0 4px 4px rgba(255, 255, 255, 0.3);
  vertical-align: middle;
}

th:first-child {
  border-top-left-radius: var(--borderRadius-md);
}
 
th:last-child {
  border-top-right-radius: var(--borderRadius-md);
  border-right: none;
}
  
tr {
  border-top: var(--border-sm-solid) var(--darkGreen);
  border-bottom: var(--border-sm-solid) var(--darkGreen);
  text-shadow: 0 1px 1px rgba(256, 256, 256, 0.1);
  cursor: pointer;
}
 
tr:hover td {
  background: var(--prusianBlue);
  color: var(--whiteText);
  border-top: var(--border-sm-solid) var(--darkGreen);
}
 
tr:first-child {
  border-top: none;
}

tr:last-child {
  border-bottom: none;
}
 
tr:nth-child(odd) td {
  background: var(--bone);
}
 
tr:nth-child(odd):hover td {
  background: var(--prusianBlue);
}

tr:last-child td:first-child {
  border-bottom-left-radius: var(--borderRadius-md);
}
 
tr:last-child td:last-child {
  border-bottom-right-radius: var(--borderRadius-md);
}

.rockets-table a {
  text-decoration: none;
  display: contents;
  color: inherit;
}
 
td {
  background: var(--alabaster);
  padding: var(--space-md);
  text-align: left;
  vertical-align: middle;
  color: var(--blackText);
  font-family: var(--fontText);
  font-size: var(--fontSize-menu);
  border-right: var(--border-sm-solid) var(--darkGreen);
}

td:last-child {
  border-right: 0px;
}
.rocket-btnBack {
  color: var(--alabaster);
  font-family: var(--fontText);
  font-size: var(--fontSize-menu);
  text-transform: uppercase;
  text-decoration: none;
  padding: var(--space-sm);
}

.rocket-container {
  padding: var(--space-xxl);
  margin-top: var(--space-md);
  display: grid;
  grid-template-columns: .5fr 1fr 1fr;
  grid-template-rows: .5fr .5fr 2fr;
  grid-template-areas:
    "img title first_flight"
    "img desc desc"
    "img overview media";
}

.bg-dark {
  background-color: #000000;
  color: var(--whiteText);
}

.bg-light {
  background-color: #CECECE;
  color: var(--blackText);
}

.rocket-img {
  grid-area: img;
  justify-self: center;
  height: 70vh;
}

.rocket-header {
  grid-area: title
}

.rocket-title,
.rocket-first_flight {
  font-family: var(--fontTitle);
}

.rocket-title {
  font-size: var(--fontSize-title);
  letter-spacing: var(--space-sm);
  margin-bottom: 0;
  margin-left:  var(--space-sm);
}

.rocket-divider__md {
  padding: 0 100%;
  border-top: var(--border-md-solid);
}

.rocket-first_flight {
  grid-area: first_flight;
  justify-self: center;
  font-size: var(--font-subtitle);
  letter-spacing: var(--space-xs);
  opacity: .3;
}

.rocket-description {
  grid-area: desc;
  line-height: var(--space-md);
}

.rocket-overview {
  grid-area: overview;
}

.rocket-overview__title {
  font-family: var(--fontTitle);
  letter-spacing: var(--space-xs);
  font-size: var(--font-subtitle);
}

.rocket-overview__details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.rocket-overview__details h4 {
  font-family: var(--fontText);
  font-size: var(--fontSize-menu);
  margin-bottom: var(--space-sm);
}

.rocket-overview__parph {
  font-family: var(--fontText);
  font-size: var(--fontSize-menu);
  margin-top: 0;
}

.rocket-overview__parph span {
  margin-left: var(--space-xs);
  opacity: .6;
  font-size: var(--fontSize-paragraph);
}

.rocket-overview__list {
  font-family: var(--fontText);
  font-size: var(--fontSize-menu);
  list-style: none;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.rocket-overview__list span {
  text-transform: capitalize;
  margin-left: var(--space-xs);
}

.rocket-media {
  grid-area: media;
  padding: var(--space-sm);
}

.rocket-wiki {
  padding: var(--space);
  text-align: end;
}

.rocket-wiki a {
  border: 1px solid var(--alabaster);
  border-radius: var(--borderRadius-md);
  background-color: transparent;
  padding: var(--space-sm);
  text-decoration: none;
  color: var(--alabaster);
  text-shadow: 0px .5px .5px rgba(0,0,0,.5);
}

